import React, { useState, useEffect } from "react";
import PortfolioList from "./PortfolioList";
import {reactJSPortfolio, TailwindCSSPortfolio, VanillajsPortfolio, featuredPortfolio} from '../data'

function Portfolio() {
  const [selected, setSelected] = useState("ft");
  const [data, setData] = useState([]);
  const list = [
    { id: "ft", title: "Featured" },
    { id: "vjs", title: "Vanilla.js" },
    { id: "tCSS", title: "TailwindCSS" },
    { id: "R", title: "React.js" },
  ];


  useEffect(()=>{
     switch (selected) {
       case 'ft':
         setData(featuredPortfolio)
         break;
       case 'vjs':
         setData(VanillajsPortfolio)
         break;
       case 'tCSS':
         setData(TailwindCSSPortfolio)
         break;
       case 'R':
         setData(reactJSPortfolio)
         break;
       default:
         setData(featuredPortfolio)
         break;
     }
  }, [selected])
  return (
    <div
      id="portfolio"
      className="h-screen card font-semibold w-full mt-24 flex flex-col items-center"
    >
      <h1 className="py-4 text-3xl sm:text-4xl text-purple-800 text-bold">Portfolio</h1>
      <ul className="m-2 p-0 flex text-sm">
        {list.map((item) => (
          <PortfolioList
            title={item.title}
            setSelected={setSelected}
            active={selected === item.id}
            id={item.id}
          />
        ))}
      </ul>
      <div className=" w-full sm:w-2/3 flex sm:items-center justify-center flex-wrap">
        {data.map((d)=>
        <div className="transition duration-500 ease-in hover:bg-purple-700 cursor-pointer relative text-white flex items-center justify-center 
        w-32 h-20 sm:w-56 sm:h-36 mx-2 my-2 rounded-lg border-b border-gray-300">
          <img
            src={d.img}
            className="w-20 sm:w-full sm:h-full object-cover z-10 rounded-t hover:opacity-20 hover:z-0 bg-purple-700"
          />
          <h3 className="absolute text-base sm:text-xl text-white">{d.title}</h3>
        </div>
        
        )}


      </div>
    </div>
  );
}

export default Portfolio;
