import React from "react";
import logo from "../assets/mobile.png";
import "react-slideshow-image/dist/styles.css";
import { Slide } from "react-slideshow-image";

function Works() {
  return (
    <div
      id="works"
      className="slide-container py-12 sm:py-40 bg-purple-700 card h-full relative"
    >
      <Slide className="px-4">
        <div className="each-slide h-h3 relative justify-center w-full">
          <div className="w-w3 mx-auto bg-white rounded-2xl flex items-center justify-center">
            <div className=" flex items-center justify-center flex-1 h-full">
              <div className="w-w4 h-h4 flex flex-col justify-between">
                <div className="w-5 sm:w-10 h-5 sm:h-10 rounded-3xl bg-yellow-300 flex items-center justify-center">
                  <img src={logo} className="w-5" />
                </div>
                <h2 className="text-xl">React + tailwind portfolio site</h2>
                <p className="text-base">
                  This site you're navigating through exists upon the foundation
                  of React and tailwind, in combinatiaton with several npm
                  packages and hooks(aos, useState, useEffect, react-slideshow).
                </p>
                <span className="text-sm font-bold underline cursor-pointer">
                  <a href="https://github.com/MuscleheadDeveloper/modelshub" target="_blank">
                    Github link
                  </a>
                </span>
                <span className="text-sm font-bold underline cursor-pointer">
                  <a href="https://muscleheaddeveloper.github.io/modelshub" target="_blank">
                    Live Version
                  </a>
                </span>
              </div>
            </div>
            <div className="flex overflow-hidden items-center justify-center flex-2 h-full">
              <img
                className="w-100 transform -rotate-12"
                src="https://d2gdtie5ivbdow.cloudfront.net/media/images/cover_react_tailwind.png"
              />
            </div>
          </div>
        </div>
        <div className="each-slide h-h3 relative w-full">
          <div className=" w-w3 mx-auto bg-white rounded-2xl flex items-center justify-center">
            <div className="left flex items-center justify-center flex-1 h-full">
              <div className="w-w4 h-h4 flex flex-col justify-between">
                <div className="w-10 h-10 rounded-3xl bg-yellow-300 flex items-center justify-center">
                  <img src={logo} className="w-5" />
                </div>
                <h2 className="text-xl">Title</h2>
                <p className="text-base">
                  Poolside convooo, about your summer last night... I'll be the
                  boyfriend in your wet dreams tonight
                </p>
                <span className="text-sm font-bold underline cursor-pointer">
                  Projects
                </span>
              </div>
            </div>
            <div className="flex overflow-hidden items-center justify-center flex-2 h-full">
              <img
                className="w-100 transform -rotate-12"
                src="https://d2gdtie5ivbdow.cloudfront.net/media/images/cover_react_tailwind.png"
              />
            </div>
          </div>
        </div>
        <div className="each-slide h-h3 relative w-full">
          <div className=" w-w3 mx-auto bg-white rounded-2xl flex items-center justify-center">
            <div className="left flex items-center justify-center flex-1 h-full">
              <div className="w-w4 h-h4 flex flex-col justify-between">
                <div className="w-10 h-10 rounded-3xl bg-yellow-300 flex items-center justify-center">
                  <img src={logo} className="w-5" />
                </div>
                <h2 className="text-xl">Nights</h2>
                <p className="text-base">
                  round the city round the clock, everybody needs you
                </p>
                <span className="text-sm font-bold underline cursor-pointer">
                  Projects
                </span>
              </div>
            </div>
            <div className="flex overflow-hidden items-center justify-center flex-2 h-full">
              <img
                className="w-100 transform -rotate-12"
                src="https://d2gdtie5ivbdow.cloudfront.net/media/images/cover_react_tailwind.png"
              />
            </div>
          </div>
        </div>
      </Slide>
    </div>
  );
}

export default Works;
