import React, {useEffect} from "react";
import Aos from "aos";
import "aos/dist/aos.css";

function Menu() {



    useEffect(() => {
      Aos.init({
        once: true,
      });
    });

  return (
      <div className=" flex flex-col items-center justify-center w-screen sm:w-80 h-screen bg-purple-700 z-10">
        <ul className="text-2xl sm:text-3xl text-white font-normal animate-pulse ">
          <li className="lists">
            <a href="#intro">Home</a>
          </li>
          <li className="lists">
            <a href="#portfolio">Portfolio</a>
          </li>
          <li className="lists">
            <a href="#works">Works</a>
          </li>
          <li className="lists">
            <a href="#testimonials">Testimonials</a>
          </li>
          <li className="lists">
            <a href="#contact">Contact</a>
          </li>
        </ul>
      </div>
  );
}

export default Menu;
